}

.width100 {
  width: 100%;
}

.container-plus {
    margin-top: 70px;
  }

.bg-navbar {
  background-color: lightgray;
  color: black;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
}

.links {
  width: 100%;
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 10px;
  background-color: teal;
  color: white;
  cursor: pointer;
}

.links:hover {
  background-color: orange;
}


.icon-large {
  font-size: 32px;
}

.menu-button-icon {
  width: 40px;
  margin-top: 5px;
}

.menu-button {
  padding-top: 10px;
  margin-left: 5px;
  width: 80px;
  height: 80px;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
}

.menu-button-content {
  margin-top: 5px;
}

.menu-button .text {
  font-family: 'Playfair Display';
  font-weight: bold;
  font-size: 9px;
  color: black;
}

.menu-button:hover {
  background-color: black;
}

.menu-button:hover .text {
  color: white;
}


/* sm Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {  
}
 
/* md Medium devices (tablets, 768px and up) The navbar toggle appears at this breakpoint */
@media (min-width: 768px) {  
}
 
/* lg Large devices (desktops, 992px and up) */
@media (min-width: 992px) { 
  body {
    xbackground-image: url(../assets/bg.jpg);
  }

  .container-plus {
    margin-top: 70px;
  }

  .menu-button {
    padding-top: 1px;
    width: 60px;
    height: 60px;
  }

  .menu-button-content {
    margin-top: 8px;
  }

  .icon-large {
    font-size: 26px;
  }

  .menu-button-icon {
    width: 50px;
    margin-top: 10px;
  }

  
  .menu-button .text {
    font-size: 8px;
    padding-top: 2px;
  }

  .xbg-navbar {
    background-color: transparent;
    position: relative;
    z-index: 0;
  }
  
}
 
/* xl Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {  
  body {
    xbackground-image: url(../assets/bg.jpg);
  }

  
  .ourpets {
    background-color: white;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .container-plus {
    margin-top: 70px;
  }

  .menu-button {
    width: 85px;
    height: 85px;
  }

  .menu-button-content {
    margin-top: 20px;
  }

  .icon-large {
    font-size: 32px;
  }

  .menu-button-icon {
    width: 60px;
    margin-top: 20px;
  }
  
  .menu-button .text {
    font-size: 9px;
    padding-top: 5px;
  }

  .xbg-navbar {
    background-color: transparent;
    position: relative;
    z-index: 0;
  }
    
}

@media (min-width: 1600px) {
  .container-plus {
    padding-left: 100px;
    padding-right: 100px;
  }
}

